/* eslint-disable */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import styles from './AuthModalContainerStyles';
import RegisterPage, { REGISTER_PAGE_NAME } from '../../pages/auth/RegisterPage';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import { getAuthInfoSelector } from '../../reducers/rootReducer';
/* eslint-enable */

class AuthModalContainer extends Component {
    static propTypes = {
      open: PropTypes.bool.isRequired,
      authInfo: PropTypes.object.isRequired
    }

    render() {
      const { open, authInfo, fullScreen } = this.props;
      const modalContent = authInfo.isRegistering ? (RegisterPage) : null;
      return (
        <Dialog
          disableBackdropClick
          disableEscapeKeyDown
          fullScreen={fullScreen}
          open={open}
          onClose={this.handleClose}
          aria-labelledby="responsive-dialog-title"
          color="inherit"
        >
          <DialogTitle id="responsive-dialog-title">
            {authInfo.isRegistering ? REGISTER_PAGE_NAME : 'Login'}
          </DialogTitle>
          {modalContent}
        </Dialog>
      );
    }
}

AuthModalContainer.propTypes = {
  fullScreen: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired
};

const StyledAuthModalContainer = withMobileDialog()(withStyles(styles)(AuthModalContainer));

const mapStateToProps = state => ({
  authInfo: getAuthInfoSelector(state)
});
const mapDispatchToProps = () => ({});
const ReduxAuthModalContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(StyledAuthModalContainer);

export default ReduxAuthModalContainer;
